import React from "react"
import { Link, graphql } from "gatsby"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import Layout from "../components/layout"
import SEO from "../components/seo"

const BlogPostTemplate = ({ data, pageContext, location }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata.title
  const { previous, next } = pageContext


  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <div class="main-header">
        <AniLink fade
          to={`/`}
        >
          ← return to blog
        </AniLink>
      </div>
      <article class="entry-article">
        <header>
          <h1 class="entry-title">{post.frontmatter.title}</h1>
          <p class="entry-date">{post.frontmatter.date}</p>
        </header>
        <section class="entry-content" dangerouslySetInnerHTML={{ __html: post.html }} />
      </article>
      <div class="navi-post">
        { previous
        ?  <div class="prev-side">
            <h2>previous post</h2>
            <AniLink fade to={previous.fields.slug} rel="prev">
                ← {previous.frontmatter.title}
            </AniLink>
          </div>
        : <div class="holder"></div>

        }
        { next 
        ? 
          <div class="next-side">
            <h2>next post</h2>
            <AniLink fade to={next.fields.slug} rel="next">
                  {next.frontmatter.title} →
            </AniLink>
          </div>
        : <div class="holder"></div>
      }
      </div>
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 400)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        featuredImage {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`
